@use "sass:list";
@use "../config/g_prefix" as g;

$brand: (
    10: #002B5B,
    20: #003F7D,
    30: #0055A4,
    40: #1976D2,
    50: #3498DB,
    60: #64B5F6,
    70: #90CAF9,
    80: #BBDEFB,
    90: #E3F2FD,
    100: #F5FAFF
);

$brand-rgb: (
    10: rgb(0, 43, 91),
    20: rgb(0, 63, 125),
    30: rgb(0, 85, 164),
    40: rgb(25, 118, 210),
    50: rgb(52, 152, 219),
    60: rgb(100, 181, 246),
    70: rgb(144, 202, 249),
    80: rgb(187, 222, 251),
    90: rgb(227, 242, 253),
    100: rgb(245, 250, 255)
);

:root {
    @each $name, $value in $brand {
        #{g.$prefix-colors}-brand-#{$name}: #{$value};
    }

     @each $name, $value in $brand-rgb {
        #{g.$prefix-colors}-brand-#{$name}-rgb: #{$value};
    }
}